.notOpened-menu {
  position: fixed;
  height: 100vh;
  width: 4vw;
  background-color: rgb(230, 152, 152);
}
.menu {
  position: fixed;
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: aquamarine;
  width: 18vw;
  z-index: 3;
  transition: margin 1s;
  margin: 0 0 0 -18vw;
}
.menu.menuOpen {
  margin: 0;
}
.menu-option {
  font-size: 25px;
  cursor: pointer;
  margin-left: 20px;
  margin-top: 10px;
  padding-right: 25%;
  display: flex;
  width: fit-content;
}
.homepage {
  position: relative;
  left: 4vw;
  width: 96vw;
  background-color: rgb(158, 203, 240);
  height: 100vh;
  display: grid;
  grid-template-columns: 48vw 48vw;
  grid-template-rows: 50vh 50vh;
}
#menu-close {
  position: absolute;
  top: 4px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
}
