.svg-container{
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding: 10%;
  position: relative;
  // height: 25rem;
  width: 90%;
  aspect-ratio: 1;
  left: 5%;
}

.skills-image{
  clip-path: polygon(50% 0, 0 100%, 100% 100%);
  width: 50%;
  height: 50%;
  position: absolute;
  z-index: 5;
  background-color: grey;
  &-1{
    left: 0;
    bottom: 0;
  }
  &-2{
    left: 50%;
    bottom: 0;
  }
  &-3{
    bottom: 50%;
    left: 25%;
  }
  &-4{
    transform: rotate(180deg);
    bottom: 0%;
    left: 25%;
  }
}
.skills-line{
  position: absolute;
  &-1{
    bottom: 0;
    left: 23%;
    height: 50%;
  }
  &-2{
    bottom: 0;
    right: 23%;
    height: 50%;
  }
  &-3{
    bottom: 46%;
    width: 100%;
  }
}


